import {
  InputOTP,
  InputOTPGroup,
  InputOTPSeparator,
  InputOTPSlot,
} from "@/components/ui/input-otp";

interface Props {
  count: number;
}

export function OTPInput({ count }: Props) {
  const half = Math.floor(count / 2);

  return (
    <InputOTP id="disabled" maxLength={count} disabled value="123456">
      <InputOTPGroup>
        {Array.from({ length: half }).map((_, index) => (
          <InputOTPSlot key={index} index={index} />
        ))}
      </InputOTPGroup>

      <InputOTPSeparator />

      <InputOTPGroup>
        {Array.from({ length: half }).map((_, index) => (
          <InputOTPSlot key={index + half} index={index + half} />
        ))}
      </InputOTPGroup>
    </InputOTP>
  );
}
